import React from 'react'
import './css/Orb.css'

const noop = () => {}

const Orb = ({
  xy,
  scale = 1,
  setScale = noop,
  className = '',
  children
}) => {
  className += ' orb'

  return (
    <div
      className={className}
      onWheel={({ deltaY }) => {
        if (deltaY > 0)
          setScale(scale + 0.02)
        else
          setScale(scale - 0.02)
      }}
      style={{
        transform: `translate(${(xy.x)}px, ${xy.y}px) scale(${scale})`
      }}
    >
      {children}
    </div>
  )
}

export default Orb
